.main {
    &[data-con="backstage"] {
        background: none;
        padding-bottom: 0;
        @include PC{
            margin-top: 108px;
        }
        @include PC-sm{
            margin-top: 54px;
        }
        a:focus,button:focus{
	outline: none !important;
	box-shadow: none !important;
}
    }
    .table-main {
        border: 1px solid $tertiary;
        &:before{
            display: none;
        }
        &>tbody>tr {
            @include PC {
                margin-bottom: 0;
            }
            &>th,
            &>td {
                padding: 16px;
            }
            &>th {
                background: $secondary-xxlight;
                font-size: $h6-font-size;
                font-weight: $font-weight-medium;
                text-align: center;
                vertical-align: middle;
                color: $black;
                @include PC {
                    display: none;
                }
            }
            &>td {
                background: $white !important;
                text-align: left;
                border-bottom: 1px solid $tertiary;
                @include PC {
                    height: auto;
                    padding: 12px !important;
                    flex-direction: column;
                    align-items: start;
                }
                &:last-child {
                    @include PC {
                        border-bottom: none;
                    }
                }
                &:before {
                    @include PC {
                        margin-bottom: 8px;
                    }
                }
            }
            label {
                color: $black;
                font-size: $h7-font-size;
            }
        }
    }
    .sign-text {
        font-weight: $font-weight-medium;
        color: $black;
        font-size: $h7-font-size;
        margin-bottom: 8px;
    }
    .sign-box {
        border-radius: 5px;
        border: 1px solid $primary-dark;
        height: 125px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: $gray-600;
        cursor: pointer;
        &:hover {
            opacity: 0.8;
        }
        &-img {
            width: 56px;
        }
    }
    .innertable {
        border: 1px solid $primary;
        th {
            background: $primary !important;
            color: $white !important;
            @include PC-xl {
                font-size: $h7-font-size !important;
            }
            @include PC-lg {
                display: none;
            }
        }
        th,
        td {
            padding: 8px !important;
            text-align: center !important;
        }
        td {
            @include PC-lg {
                display: flex;
                flex-direction: row !important;
                text-align: left !important;
                align-items: center;
                padding: 0 20px !important;
                height: 38px;
                position: relative;
                font-size: $h7-font-size;
            }
            &:last-child{
                @include PC{
                    border-bottom: none;
                }
                @include PC-lg {
                    border-bottom: 1px solid $tertiary;
                }
            }
            &::before {
                content: "";
                display: none;
                @include PC-lg {
                    display: inline-block;
                    flex-shrink: 0;
                    content: attr(data-th) "";
                    color: $primary-xdark;
                    font-weight: $font-weight-medium;
                    margin-bottom: 0 !important;
                }
            }
            &:after{
                display: none;
            }
            &:first-child{
                @include PC-lg {
                    justify-content: flex-start;
                    background: $primary !important;
            color: $white !important;
            &:before{
                color: $white;
            }
            &::after{
                    content: "-";
                    display: block;
                    margin-left: auto;
                    font-size: $h3-font-size;
                    line-height: 0.5;
                    margin-top: -7px;
                }
                }
            }
        }
    }
}
.photo-upload {
    border-radius: 10px;
    border: 1px solid $gray-400;
    padding: 14px 14px 14px 8px;
    display: flex;
    @include mobile-lg {
        flex-direction: column;
    }
    &-function {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 24px;
        flex-shrink: 0;
        margin-right: 8px;
        @include mobile-lg {
            flex-direction: row;
            justify-content: flex-start;
            width: 80px;
            margin-bottom: 8px;
        }
        p {
            display: block;
            color: $primary-xdark;
            border-bottom: 1px solid $gray-400;
            padding-bottom: 8px;
            margin-bottom: 8px;
            text-align: center;
            @include mobile-lg {
                border-right: 1px solid $gray-400;
                border-bottom: none;
                margin-bottom: 0;
                padding: 0 12px;
                margin-right: 12px;
            }
        }
    }
    &-img {
        border-radius: 10px;
        width: 170px;
        height: 120px;
        overflow: hidden;
        cursor: pointer;
        flex-shrink: 0;
        margin-right: 8px;
        display: flex;
        justify-content: center;
        @include PC {
            width: 150px;
            height: 100px;
        }
        @include mobile-lg {
            margin-bottom: 12px;
        }
        & > img {
            height: 100%;
        }
    }
}
#showpic {
    width: 100%;
    height: 100vh;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    display: none;
    background: rgba(0, 0, 0, 0.9);
    position: fixed;
    left: 0;
    top: 0;
    z-index: 2010;
    margin: 0 auto;
    transition: all 0.2s ease;
    .boxclose {
        position: absolute;
        width: 32px;
        height: 32px;
        right: 30px;
        top: 30px;
        cursor: pointer;
        &:hover {
            opacity: 0.8;
        }
    }
    .showpic-img {
        width: 80%;
        max-width: 1000px;
        border-radius: 10px;
        overflow: hidden;
    }
}
.photo-add {
    border-radius: 10px;
    background: $gray-200;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $gray-500 !important;
    font-size: $h6-font-size;
    font-weight: $font-weight-medium;
    height: 100%;
    padding: 16px 0;
    &:hover {
        background: none;
    }
    img {
        width: 70px;
    }
}