/**/
.main {
    &[data-con="search"] {
        margin-top: 54px;
        padding-top: 64px;
        padding-bottom: 150px;
        background:
            url("../images/img-butterfly.svg") no-repeat 95% 165px,
            url("../images/footer-bg-1.png") no-repeat left bottom,
            url("../images/footer-bg-2.png") no-repeat right bottom,
            url("../images/footer-bg-3.png") no-repeat 2% bottom;
        background-size: 132px, 1063px, 1070px, 949px;
        @include PC-md{
            background-size: 132px, 700px, 800px, 749px;
        }
        @include PC-sm{
            padding-top: 54px;
            background-size: 132px, 600px, 700px, 649px;
        }
        @include Pad{
            padding-top: 40px;
        }
        .main-container{
            border-radius: 20px;
            &:before,
            &:after {
                content: "";
                display: block;
                position: absolute;
            }
            &:before {
                background: url("../images/img-animal-top.svg");
                background-size: cover;
                width: 388px;
                height: 100px;
                top: -100px;
                left: 40px;
                @include PC {
                    width: 300px;
                    height: 77px;
                    top: -77px;
                }
                @include Pad {
                    width: 213px;
                    height: 55px;
                    top: -55px;
                }
            }
            &:after {
                background: url("../images/img-photo-top.png");
                background-size: cover;
                width: 280px;
                height: 212px;
                top: -114px;
                right: 72px;
                @include PC {
                    width: 240px;
                    height: 182px;
                    top: -90px;
                }
                @include PC-sm {
                    width: 208px;
                    height: 157px;
                    top: -74px;
                }
                @include Pad {
                    display: none;
                }
            }
        }
    }
    &[data-item="calc"] {
        .main-container {
            border-radius: 0 0 20px 20px;
            padding-top: 30px;
            &:before,
            &:after {
                display: none;
            }
        }
        .main-container-header{
            &:after {
                content: "";
                display: block;
                background: url("../images/img-bird-top.svg");
                background-size: cover;
                width: 436px;
                height: 77px;
                position: absolute;
                right: 24px;
                top: -77px;
                @include PC {
                    width: 365px;
                    height: 66px;
                    top: -66px;
                }
                @include PC-sm {
                    width: 311px;
                    height: 56px;
                    top: -56px;
                }
                @include Pad {
                    width: 290px;
                    height: 52px;
                    top: -52px;
                }
            }
        }
    }
}
/*--------------------------------------*/
.navbar[data-con="search"] {
    .navbar-brand {
        position: relative;
        z-index: 100;
        height: 54px;
    }
    .navbar-collapse {
        background: $tertiary-light;
        top: 0;
        @include PC-sm {
            top: 54px;
        }
        .navbar-main {
            .nav-item {
                &:nth-last-child(3) {
                    &::after {
                        display: block;
                        @include PC-sm {
                            display: none !important;
                        }
                    }
                }
            }
            .nav-link {
                color: $primary-xdark;
                @include PC-sm {
                    font-size: $h6-font-size;
                }
            }
        }
    }
}
