/*--------------------------------------*/
.key {
    color: $gray-300;
    font-size: $h8-font-size;
}
/*-----------------()---------------------*/
.carousel-indicators {
    bottom: -45px;
    [data-bs-target] {
        width: 12px;
        height: 12px;
        margin-right: 8px;
        margin-left: 8px;
        border-radius: 50%;
        background: $primary;
        border: 2px solid $white;
    }
    .active {
        background: $primary-xxdark;
    }
}
/*--------------------------------------*/
.sub-title {
    font-size: $h5-font-size;
    border-bottom: 1px solid $primary;
    font-weight: $font-weight-bold;
    padding-bottom: 8px;
    display: flex;
    align-items: center;
    @include Pad {
        font-size: $h6-font-size;
        line-height: 1.5;
    }
    img {
        width: 32px;
        margin-right: 8px;
    }
}
/*--------------------------------------*/
.link-img {
    display: inline-block;
    &:hover {
        opacity: 0.8;
    }
}
.link-list {
    display: flex;
    @include mobile-lg {
        flex-direction: column;
    }
    &-item {
        border-right: 1px solid $gray-400;
        @include mobile-lg {
            border-right: none;
            border-bottom: 1px solid $gray-400;
        }
        &:last-child {
            border-right: 0;
            @include mobile-lg {
                border-bottom: none;
            }
        }
    }
    &-link {
        display: block;
        font-weight: $font-weight-medium;
        padding: 0 16px;
        @include Pad {
            font-size: $h7-font-size;
        }
        @include mobile-lg {
            padding: 4px 0;
        }
    }
}
/*--------------------------------------*/
.nav-tabs {
    .nav-item {
        margin-bottom: 0;
    }
    .nav-link {
        background: $primary-light;
        color: $primary-dark;
        border-radius: 8px;
        padding: 4px 40px !important;
        @include PC {
            padding: 4px 32px !important;
        }
        &.active {
            background: $primary-dark;
            color: $white;
        }
    }
}
/*--------------------------------------*/
.radio-list {
    display: flex;
    flex-wrap: wrap;
    &-item {
        margin-right: 12px;
        margin-bottom: 12px;
        &:last-child {
            margin-right: 0;
        }
    }
    &-btn {
        border: 1px solid $primary;
        box-shadow: 0 2px 2px $gray-500;
        @include Pad {
            font-size: $h7-font-size;
        }
        &:hover {
            border: 1px solid $primary;
            box-shadow: none;
        }
        &.active {
            color: $secondary-xdark;
            background: $secondary;
            box-shadow: none;
            border: none;
        }
    }
}
.shadow-link {
    &:hover {
        box-shadow: none !important;
    }
}
.opacity-link {
    &:hover {
        opacity: 0.8;
    }
}
/*--------------------------------------*/
.step {
    max-width: 880px;
    margin: 0 auto;
    display: flex;
    background: url("../images/dots.svg") repeat-x center 40px;
    position: relative;
    @include Pad {
        background: url("../images/dots.svg") repeat-x center 30px;
        background-size: 200px;
    }
    @include mobile-lg {
        width: 425px;
    }
    &:before,
    &:after {
        content: "";
        display: block;
        position: absolute;
        width: 100px;
        height: 50px;
        background: $tertiary-xlight;
        top: 30px;
        @include Pad {
            width: 50px;
        }
    }
    &:before {
        left: 0;
    }
    &:after {
        right: 0;
    }
    &-item {
        display: flex;
        align-items: center;
        flex-direction: column;
        position: relative;
        z-index: 10;
        &-img {
            width: 80px;
            height: 80px;
            background: $white;
            border-radius: 50%;
            border: 3px solid $base;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            margin-bottom: 4px;
            @include Pad {
                width: 60px;
                height: 60px;
                border-width: 2px;
            }
            @include mobile-lg {
                width: 50px;
                height: 50px;
            }
            img {
                width: 40px;
                @include Pad {
                    width: 30px;
                }
                @include mobile-lg {
                    width: 20px;
                }
            }
            span {
                background: $base;
                color: $white;
                width: 25px;
                height: 25px;
                display: flex;
                justify-content: center;
                padding-top: 1px;
                border-radius: 50%;
                position: absolute;
                right: -10px;
                top: 0;
                line-height: 1;
                border: 2px solid $white;
                @include mobile-lg {
                    width: 20px;
                    height: 20px;
                    font-size: $h8-font-size;
                }
            }
        }
        &-txt {
            color: $base-dark;
            font-size: $h6-font-size;
            font-weight: $font-weight-bold;
            text-align: center;
            @include Pad {
                font-size: $h7-font-size;
            }
        }
        &[data-status="now"] {
            .step-item-img {
                border: 7px solid $secondary-dark;
                @include Pad {
                    border-width: 4px;
                }
                span {
                    background: $secondary-dark;
                    color: $white;
                    top: -7px;
                }
                img {
                    filter: invert(52%) sepia(57%) saturate(456%) hue-rotate(85deg) brightness(101%) contrast(90%);
                }
            }
            .step-item-txt {
                color: $gray-700;
            }
        }
        &[data-status="done"] {
            .step-item-img {
                background: $secondary-dark;
                border: none;
                span {
                    background: $secondary-light;
                    color: $white;
                    top: 0;
                }
                img {
                    filter: invert(52%) sepia(57%) saturate(456%) hue-rotate(85deg) brightness(500%) contrast(90%);
                }
            }
            .step-item-txt {
                color: $gray-700;
            }
        }
    }
}
/*--------------------------------------*/
.text-basic {
    font-size: $h7-font-size;
    font-weight: $font-weight-medium;
    @include Pad {
        font-size: $h8-font-size;
    }
}
/*--------------------------------------*/
.text-list {
    display: flex;
    @include PC-sm {
        flex-direction: column;
    }
    &-item {
        display: flex;
        font-size: $h7-font-size;
        font-weight: $font-weight-medium;
        position: relative;
        color: $gray-800;
        img {
            opacity: 0.8;
        }
    }
}
.notice-list {
    @include PC-sm {
        flex-direction: column;
    }
    &-item {
        font-size: $h7-font-size;
        color: $bright;
        font-weight: $font-weight-medium;
        margin-left: 22px;
        position: relative;
        &:before {
            content: "";
            display: block;
            color: $bright;
            position: absolute;
            left: -22px;
        }
    }
}
.number-list {
    /**/
    padding-left: 24px;
    font-size: $h7-font-size;
    @include Pad {
        font-size: $h8-font-size;
    }
    &-item {
        list-style-type: decimal;
        margin-bottom: 4px;
    }
}
.dot-list {
    &-item {
        display: flex;
        align-items: start;
        font-size: $h7-font-size;
        margin-bottom: 4px;
        &::before {
            content: "";
            display: inline-block;
            width: 7px;
            height: 7px;
            background: $danger;
            margin-right: 12px;
            flex-shrink: 0;
            margin-top: 8px;
        }
    }
}
.tag-list {
    background: $white;
    &-item {
        border-bottom: 1px solid $primary-light;
        &:last-child {
            border-bottom: none;
        }
    }
    &-link {
        display: flex !important;
        align-items: start;
        color: $gray-800;
        padding: 16px 0;
        font-size: $h6-font-size;
        @include PC-sm {
            padding: 12px 0;
        }
        @include mobile-lg {
            font-size: $h7-font-size;
        }
        &:before {
            content: "";
            display: block;
            width: 7px;
            height: 17px;
            margin-right: 16px;
            margin-top: 2px;
            background: $tertiary;
            flex-shrink: 0;
            @include mobile-lg {
                margin-right: 12px;
            }
        }
        &:hover {
            text-decoration: underline !important;
        }
        p {
            @include over_text(2);
        }
    }
}
.icon-list {
    display: flex;
    @include Pad {
        flex-direction: column;
    }
    &-item {
        display: flex;
        margin-right: 16px;
        @include Pad {
            margin-bottom: 8px;
        }
        &:last-child {
            margin-right: 0;
            margin-bottom: 0;
        }
        img {
            width: 20px;
            margin-right: 8px;
        }
    }
}
.text-word-list {
    &-item {
        display: flex;
    }
    span {
        flex-shrink: 0;
    }
}
.news {
    &-title {
        background: $secondary-pale-dark;
        color: $white;
        display: flex;
        justify-content: center;
        padding: 12px 0;
        font-size: $h4-font-size;
    }
    &-list {
        li:nth-child(odd) {
            .news-list-link {
                display: block;
                background: lighten($color: $bright-xxlight, $amount: 25%);
            }
        }
        &-tag {
            display: inline-block;
            line-height: 1;
            color: #000 !important;
            border-right: 1px solid $bright;
            padding-right: 12px;
            margin-right: 12px;
            font-weight: $font-weight-semibold;
            @include PC-sm{
                border-right: none;
            }
        }
        &-link {
            display: flex;
            align-items: center;
            padding: 16px 16px;
            background: lighten($color: $bright-xxlight, $amount: 28%);
            font-size: $h6-font-size;
            color: $gray-900;
            @include PC-sm{
                flex-wrap: wrap;
            }
            &:hover {
                background: $white !important;
            }
            span {
                color: $bright-dark;
                font-weight: $font-weight-semibold;
                display: inline-block;
                padding-right: 12px;
            }
            p {
                display: inline;
                font-weight: $font-weight-medium;
                @include PC-sm {
        display: block;
        width: 100%;
    }
            }
        }
    }
}
/*--------------------------------------*/
.table-title {
    font-size: $h3-font-size;
    font-weight: $font-weight-semibold;
    text-align: center;
    @include Pad {
        font-size: $h5-font-size;
    }
}
.table-rwd {
    min-width: 100%;
    border: 1px solid $primary-xdark;
    position: relative;
    @include PC {
        min-width: 90%;
        margin: 0 auto;
        border: none;
    }
    &:before {
        content: " ";
        display: block;
        width: 100%;
        height: 1px;
        background: $primary-xdark;
        position: absolute;
        left: 0;
        bottom: 0;
        @include PC {
            display: none;
        }
    }
    & > thead {
        @include PC {
            display: none;
        }
    }
    & > tbody > tr {
        border-bottom: 1px solid $primary-light;
        @include PC {
            display: block;
            margin-bottom: 16px;
        }
        &:nth-child(odd) {
            td {
                background: $primary-xlight;
                @include PC {
                    background: $white;
                }
            }
        }
        &:last-child {
            @include PC {
                margin-bottom: 0;
            }
        }
        &.hide {
            td:first-child {
                &::after {
                    content: "+";
                    font-size: $h5-font-size;
                    line-height: 1;
                    margin-top: 0;
                }
            }
        }
    }
    & > tbody > tr > th {
        display: table-cell;
        background: $primary-xdark;
        color: $white;
        font-weight: normal;
        vertical-align: middle;
        font-size: $h7-font-size;
        padding: 8px;
        text-align: center;
    }
    & > tbody > tr > td {
        display: table-cell;
        vertical-align: middle;
        padding: 16px 8px;
        text-align: center;
        font-size: $h7-font-size;
        font-weight: $font-weight-medium;
        @include PC {
            display: flex;
            text-align: left !important;
            align-items: center;
            padding: 0 20px !important;
            height: 38px;
            position: relative;
            font-size: $h7-font-size;
            &[data-th=""] {
                &::before {
                    content: "";
                    display: none;
                }
            }
        }
        &:first-child {
            text-align: center;
            font-weight: $font-weight-normal;
            padding: 8px 20px !important;
            pointer-events: none;
            @include PC {
                display: flex;
                justify-content: space-between;
                background: $primary-xdark !important;
                color: $white;
                pointer-events: initial;
                &::after {
                    content: "-";
                    display: block;
                    margin-left: auto;
                    font-size: $h3-font-size;
                    line-height: 0.5;
                    margin-top: -7px;
                }
                &:before {
                    color: $white;
                    font-weight: $font-weight-normal;
                }
            }
        }
        &:before {
            display: none;
            @include PC {
                flex-shrink: 0;
                content: attr(data-th) "";
                display: inline-block;
                color: $primary-xdark;
                font-weight: $font-weight-medium;
            }
        }
    }
    a {
        color: $bright;
        &:hover {
            opacity: 0.8;
        }
    }
    &[data-con="secondary"] {
        border: none;
        &:before {
            display: none;
        }
        th {
            background: $secondary-light;
            opacity: 0.8;
            color: $black;
            font-weight: $font-weight-bold;
        }
        tr {
            border-color: $white;
            &:nth-child(odd) {
                td {
                    background: $secondary-xxlight;
                }
            }
            &:nth-child(even) {
                td {
                    background: lighten($color: $secondary, $amount: 20%);
                }
            }
        }
        td:first-child {
            @include PC {
                background: $secondary-light !important;
                color: $black !important;
                &:before {
                    color: $black;
                }
            }
        }
        td:last-child {
            @include PC {
                &::before {
                    display: none;
                }
            }
        }
    }
    &[data-con="tertiary"] {
        border: none;
        &:before {
            display: none;
        }
        th {
            background: $tertiary;
            opacity: 0.8;
            color: $black;
            font-weight: $font-weight-bold;
        }
        tr {
            border-color: $white;
            &:nth-child(odd) {
                td {
                    background: lighten($color: $tertiary, $amount: 20%);
                }
            }
            &:nth-child(even) {
                td {
                    background: lighten($color: $tertiary, $amount: 15%);
                }
            }
        }
        td:first-child {
            @include PC {
                background: $tertiary !important;
                color: $black !important;
                &:before {
                    color: $black;
                }
            }
        }
        td:last-child {
            @include PC {
                &::before {
                    display: none;
                }
            }
        }
    }
}
.table-link {
    display: flex;
    justify-content: center;
}
.table-toggle {
    cursor: pointer;
}
/*--------------------------------------*/
.scrollbar {
    width: 100%;
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar-track {
        background-color: $gray-300;
    }
    &::-webkit-scrollbar {
        width: 4px;
        height: 4px;
        background-color: $gray-300;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: $gray-500;
    }
}
/*--------------------------------------*/
.input-search {
    display: flex;
    align-items: center;
    label {
        font-size: $h5-font-size;
    }
    input {
        border-radius: 50px 0 0 50px !important;
        border: 1px solid $primary !important;
        border-right: 0 !important;
        padding-left: 20px;
        height: 45px;
        @include PC-sm {
            height: 34px;
        }
        &:focus {
            box-shadow: none;
        }
        &::placeholder {
            color: $gray-600 !important;
            @include PC-sm {
                font-size: $h7-font-size;
            }
        }
    }
    input ~ button {
        border-radius: 0 50px 50px 0 !important;
        border: 1px solid $primary;
        border-left: 0;
        background: $white !important;
        height: 45px;
        @include PC-sm {
            height: 34px;
        }
        &:hover {
            img {
                opacity: 0.8;
            }
        }
    }
}
/*--------------------------------------*/
.modal {
    &-header {
        align-items: flex-start;
        border-bottom: none;
        position: relative;
        font-size: $h5-font-size;
        font-weight: $font-weight-semibold;
        padding-left: 24px;
        padding-right: 24px;
        &:after {
            content: "";
            display: block;
            position: absolute;
            left: 24px;
            bottom: 0;
            width: calc(100% - 48px);
            height: 1px;
            background: $primary-light;
        }
        .btn-close {
            background: url("../images/ic-close.svg") no-repeat center;
            background-size: 80%;
            opacity: 1;
            @include text-indent;
            &:hover {
                opacity: 0.8;
            }
        }
    }
    &-content {
        border-radius: 16px !important;
    }
    &-body {
        font-size: $h6-font-size;
        @include mobile-lg {
            font-size: $h7-font-size;
        }
    }
}
/*--------------------------------------*/
.accordion {
    &-body {
        position: relative;
        @include Pad {
            font-size: $h6-font-size;
        }
        &::before {
            content: "";
            display: block;
            position: absolute;
            width: calc(100% - 32px);
            height: 1px;
            left: 16px;
            top: 0;
            background: $primary-light;
        }
    }
    &-item {
        border: none;
    }
    &-button {
        background: $white;
        color: $primary;
        border-radius: 8px !important;
        padding: 12px 48px 12px 32px;
        position: relative;
        line-height: 1.3;
        box-shadow: none !important;
        font-size: $h5-font-size;
        font-weight: $font-weight-medium;
        @include Pad {
            padding: 12px 32px 12px 18px;
        }
        &:after {
            background: url(../images/ic-add-active.svg);
            background-size: 100%;
            position: absolute;
            right: 32px;
            top: calc(50% - 10px);
            width: 24px;
            height: 24px;
            @include Pad {
                right: 16px;
                width: 18px;
                height: 18px;
                top: calc(50% - 9px);
            }
        }
        &:not(.collapsed) {
            background: $white;
            color: $secondary;
        }
        &:not(.collapsed)::after {
            background: url(../images/ic-minus-default.svg);
            background-size: 100%;
            transform: rotate(0deg);
        }
    }
}
/*--------------------------------------*/

.page {
    display: flex;
    justify-content: center;
    align-items: center;
    @include mobile-lg {
        flex-direction: column;
        align-items: center;
    }
    &-list {
        display: flex;
        justify-content: flex-end;
        border-right: 1px solid $gray-300;
        padding-right: 16px;
        margin-right: 16px;
        @include mobile-lg {
            margin-bottom: 12px;
            border-right: none;
            padding-right: 0;
            margin-right: 0;
        }
    }
    &-item {
        padding: 0 4px;
        display: flex;
        align-items: center;
        &:nth-child(1),
        &:nth-child(2),
        &:nth-last-child(1),
        &:nth-last-child(2) {
            .page-link {
                border: 1px solid $gray-200;
                background: $gray-100;
            }
        }
        .form-control {
            width: 40px;
            height: 28px;
            color: $primary;
            border: 1px solid $primary;
            border-radius: 5px;
            margin: 0 5px;
            padding: 0 8px;
            text-align: center;
        }
    }
    &-link {
        border-radius: 4px;
        background: $white;
        border: 1px solid $primary;
        color: $primary;
        padding: 0;
        width: 28px;
        height: 28px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.2s ease;
        &:hover {
            background: $gray-100;
        }
        &.active {
            border: 2px solid $primary;
            font-weight: $font-weight-medium;
        }
        img {
            width: 25px;
            margin-right: 0;
        }
        &-function {
            border-radius: 4px;
        }
    }
    &-form {
        display: flex;
        align-items: center;
        flex-shrink: 0;
        color: $gray-600;
        span {
            flex-shrink: 0;
        }
        select {
            width: 64px;
            height: 28px;
            flex-shrink: 0;
            padding-top: 0;
            padding-bottom: 0;
        }
    }
}
/*--------------------------------------*/
.file-container {
    display: flex;
    align-items: stretch;
    border-radius: 8px;
    border: 1px solid $secondary-xdark;
    padding: 4px;
    background: $white;
    width: 100%;
    span {
        background: $secondary-xdark;
        border-radius: 5px;
        padding: 0 8px;
        line-height: 0;
        color: $white;
        margin-right: 12px;
        display: flex;
        align-items: center;
    }
    p {
        font-size: $h7-font-size;
    }
}
/*-----------------mobile---------------------*/
.border-mobile-0 {
    @include mobile-lg {
        border: none !important;
    }
}
.input-pad-100 {
    @include Pad {
        width: 100% !important;
    }
}

.input-mobile-100 {
    @include mobile-lg {
        width: 100% !important;
    }
}
