.container {
	width: 90%;
	max-width: 1180px;
	@include PC-sm {
		width: 95%;
		max-width: 1920px;
	}
	@include mobile-md {
		max-width: 98%;
	}
}
.main {
	margin-top: 108px;
	margin-bottom: -2px;
	padding-bottom: 150px;
	background:
		url("../images/footer-bg-index-1.png") no-repeat left bottom,
		url("../images/footer-bg-index-2.png") no-repeat right bottom,
		url("../images/footer-bg-index-3.png") no-repeat center bottom;
	background-size: 1000px, 1091px, 100%;
	@include PC-md{
		background-size: 1000px, 1091px, 120%;
	}
	@include PC {
		margin-top: 49px;
		background-size: 800px, 990px, 160%;
	}
	@include PC-sm {
		background-size: 700px, 890px, 200%;
	}
	@include mobile-lg {
		background-size: 500px, 690px, 250%;
	}
	&[data-con="hill"]{
		background:
		url("../images/bg-bottom-up.svg") no-repeat center bottom,
		url("../images/bg-bottom.svg") no-repeat center bottom;
		@include PC {
		margin-top: 49px;
		background-size: 100%, 100%;
		padding-bottom: 100px;
	}
	@include mobile-lg {
		padding-bottom: 70px;
	}
	}
}

/*--------------------------------------*/
.login {
	position: relative;
	display: flex;
	align-items: stretch;
	@include PC-sm {
		flex-direction: column;
	}
	&-close {
		position: absolute;
		right: 16px;
		top: 16px;
		background: url("../images/ic-modal-close.svg");
		background-size: cover;
	}
	&-vision {
		display: flex;
		align-items: center;
		width: 45%;
		padding: 0 24px;
		@include PC-sm {
			width: 100%;
			justify-content: center;
			padding: 24px;
		}
		img {
			@include PC-sm {
				max-width: 200px;
			}
			@include mobile {
				max-width: 160px;
			}
		}
	}
	&-title {
		color: $primary-xdark;
		font-weight: $font-weight-medium;
		font-size: $h3-font-size;
		text-align: center;
		@include mobile-lg {
			font-size: $h4-font-size;
		}
	}
	&-form {
		width: 55%;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 32px 40px;
		@include PC-sm {
			width: 100%;
			padding: 24px 40px 32px 40px;
		}
		@include Pad {
			padding: 24px;
		}
	}
	input {
		border-radius: 50rem;
		border: none !important;
		background: $primary-light;
		padding-left: 20px;
		height: 40px;
		font-size: $h6-font-size;
		font-weight: $font-weight-medium;
		@include PC-md {
			height: auto;
		}

		&::placeholder {
			color: $gray-800 !important;
		}
		&[data-con="account"] {
			background: url("../images/ic-account.svg") no-repeat 12px center $primary-xlight;
			padding-left: 55px;
			@include mobile {
				background-size: 26px;
				padding-left: 45px;
			}
		}
		&[data-con="password"] {
			background: url("../images/ic-password.svg") no-repeat 12px center $primary-xlight;
			padding-left: 55px;
			@include mobile {
				background-size: 26px;
				padding-left: 45px;
			}
		}
		&[data-con="pin"] {
			background: url("../images/ic-pin.svg") no-repeat 12px center $primary-xlight;
			padding-left: 55px;
			@include mobile {
				background-size: 26px;
				padding-left: 45px;
			}
		}
	}
	&-link {
		display: flex;
		align-items: center;
		font-weight: $font-weight-semibold;
	}
	&-function {
		&-item {
			height: 20px;
		}
	}
	&-btn {
		height: 50px;
		font-size: $h5-font-size;
		@include mobile {
			font-size: $h6-font-size;
		}
	}
	.form-label {
		color: $black;
		font-size: $h4-font-size;
		font-weight: $font-weight-normal;
		@include PC-sm {
			font-size: $h5-font-size;
		}
		@include mobile {
			font-size: $h6-font-size;
			font-weight: $font-weight-semibold;
		}
		span {
			color: $primary;
			font-size: $h6-font-size;
			padding-left: 8px;
			@include PC-sm {
				font-size: $h7-font-size;
			}
			@include mobile {
				font-size: $h8-font-size;
			}
		}
	}
}

/*--------------------------------------*/
.navbar {
	background: $white;
	height: 54px;
	justify-content: center;
	width: 100%;
	&-brand {
		color: $gray-700;
		display: flex;
		align-items: center;
		letter-spacing: 3px;
		@include mobile {
			letter-spacing: 0;
		}
		h1 {
			font-size: $h5-font-size;
			@include mobile-lg {
				font-size: $h6-font-size;
			}
		}
	}
	&-toggler {
		border: none;
		width: 30px;
		height: 30px;
		@include text-indent;
		background: url("../images/ic-close.svg") no-repeat center;
		background-size: cover;
		&.collapsed {
			background: url("../images/ic-menu.svg") no-repeat center;
			background-size: cover;
		}
	}
	&-collapse {
		position: fixed;
		left: 0;
		top: 54px;
		width: 100%;
		background: $tertiary;
		@include PC-sm {
			box-shadow: 0 4px 3px $gray-500;
			left: 0;
			width: 100%;
		}
	}
	&-main {
		/**/
		width: 100%;
		.nav-item {
			position: relative;
			@include PC-sm {
				width: 100%;
			}
			&:after {
				position: absolute;
				content: "";
				width: 1px;
				height: calc(100% - 24px);
				right: 0;
				top: 12px;
				background: $white;
				@include PC-sm {
					display: none;
				}
			}
			&:nth-last-child(3) {
				&::after {
					display: none;
				}
			}
			&:last-child {
				&::after {
					display: none;
				}
				.nav-link {
					padding-right: 0 !important;
					@include PC-sm {
						padding-right: 32px !important;
					}
				}
			}
		}
		.nav-link {
			padding: 12px 40px !important;
			font-size: $h5-font-size;
			font-weight: $font-weight-bold;
			@include PC {
				padding: 12px 32px !important;
			}
			@include PC-sm {
				font-size: $h6-font-size;
			}
			&:hover {
				color: $black;
			}
		}
	}
}
.navbar-collapse[data-style="backstage"]{
	background: $primary;
	.nav-link{
		color: $white;
		font-weight: $font-weight-medium;
		&:hover{
			color: $special-xlight;
		}
	}
}
.key {
	position: fixed;
	left: 4px;
	top: 8px;
}
.nav {
	&-item {
		display: flex;
		align-items: center;
		position: relative;
		@include PC {
			font-size: $h7-font-size;
		}
		@include PC-sm {
			border-bottom: 1px solid $primary-light;
		}
		&:last-child,
		&:nth-last-child(2),
		&:nth-last-child(3) {
			&::before {
				display: none;
			}
		}
		&:before {
			content: "";
			display: block;
			width: 1px;
			height: 24px;
			background: $gray-300;
			position: absolute;
			right: 0;
			top: calc(50% - 12px);
			@include PC-sm {
				display: none;
			}
		}
	}
	&-link {
		display: block;
		color: $gray-600;
		padding: 12px 20px !important;
		position: relative;
		font-weight: $font-weight-medium;
		@include PC {
			padding: 16px 10px !important;
			font-size: $h7-font-size;
		}
		@include PC-sm {
			width: 100%;
			padding: 12px 16px !important;
			text-align: center;
		}
		&:hover {
			color: $primary;
		}
	}
}
/*--------------------footer------------------*/
.footer {
	background: $secondary-xlight;
	position: relative;
	padding: 40px 0;
	margin-top: -1px;
	&-start {
		width: 100%;
		margin-right: 72px;
		@include PC {
			margin-right: 0;
		}
	}
	&-end {
		width: 180px;
		display: flex;
		flex-shrink: 0;
		flex-direction: column;
		align-items: stretch;
		@include PC {
			width: 100%;
			flex-direction: row;
			align-items: center;
		}
		@include mobile-lg {
			flex-direction: column;
			align-items: flex-start;
			& > * {
				margin-bottom: 12px;
			}
		}
	}
	&-btn {
		background: $white !important;
		color: $primary-xdark;
		justify-content: flex-start;
		margin-bottom: 16px;
		@include Pad {
			font-size: $h7-font-size;
			padding: 2px 16px;
			text-align: left;
		}
		@include mobile-lg {
			margin-bottom: 12px;
			width: 100%;
			justify-content: space-between;
		}
		&:hover {
			border: 1px solid $primary;
		}
	}
	&-function {
		display: flex;
		flex-direction: column;
		position: fixed;
		right: 12px;
		bottom: 16px;
		z-index: 100;
		&-link {
			width: 48px;
			height: 48px;
			border-radius: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
			font-weight: $font-weight-semibold;
			font-size: $h8-font-size;
			color: $white;
			background: $primary;
			@include mobile-lg {
				width: 40px;
				height: 40px;
			}
			&:hover {
				background-color: $white;
				color: $primary;
			}
		}
	}
	.key {
		position: absolute;
		left: 16px;
		top: 16px;
	}
}

/*--------------------------------------*/
.backdrop {
	width: 100vw;
	height: 100vh;
	position: absolute;
	left: 0;
	top: 0;
	display: none;
	z-index: 100;
	&.show {
		display: block;
	}
}
.offcanvas-backdrop.show {
	opacity: 0.7;
}
.offcanvas-header {
	padding-left: 20px;
	padding-right: 32px;
	.btn {
		&:active,
		&:focus {
			border: 0;
		}
	}
}
.offcanvas-bottom {
	border-radius: 24px 24px 0 0;
	height: 350px !important;
	padding: 24px 0;
	.offcanvas-header {
		position: relative;
		&-close {
			position: absolute;
			background: $gray-100 !important;
			border-radius: 24px 24px 0 0;
			width: 106px;
			height: 40px;
			top: -39px;
			left: calc(50% - 53px);
			display: flex;
			justify-content: center;
			align-items: center;
			border: none;
			&::before,
			&::after {
				content: "";
				display: block;
				position: absolute;
				width: 24px;
				height: 24px;
				background: url("../images/fillet-gray.svg");
			}
			&::before {
				bottom: -1px;
				left: -23px;
			}
			&::after {
				bottom: -1px;
				right: -23px;
				transform: rotate(90deg);
			}
			&:focus {
				background: $gray-100;
			}
		}
	}
	.offcanvas-body {
		padding: 0 66px;
		overflow-y: initial;
	}
}
.offcanvas-end {
	width: 350px !important;
	border-radius: 24px 0 0 24px;
	.offcanvas-header {
		border-radius: 24px 0 0 0;
	}
}
.offcanvas-body {
	padding: 0 32px 24px 32px;
}

.select-list-wrapper {
	overflow-y: auto;
	padding: 0 32px 24px 32px;
	flex: 1 1;
	font-size: $h6-font-size;
	.form-check-input {
		width: 16px;
		height: 16px;
		margin-top: 0;
		border: 2px solid $gray-700 !important;
		&[type="radio"] {
			border-radius: 50%;
		}
		&:checked[type="radio"] {
			border-radius: 50%;
			background: url(../images/radio-circle.svg) no-repeat center $primary;
			background-size: 6px;
			border: 2px solid $primary !important;
		}
		&[type="checkbox"] {
			border-radius: 4;
		}
		&:checked[type="checkbox"] {
			background: url(../images/ic-check.svg) no-repeat center $primary;
			background-size: 9px;
			border: 2px solid $primary !important;
		}
	}
	input[disabled] {
		border: 2px solid $gray-600;
	}
	input[disabled] ~ label {
		opacity: 1 !important;
		color: $gray-700;
	}
	input[disabled] ~ label span {
		color: $danger !important;
	}
	label {
		display: flex;
		justify-content: space-between;
		padding-left: 4;
		p {
			display: block;
			width: calc(100% - 70px);
			flex: 1 1;
			line-height: 1.3;
		}
		span {
			padding-left: 8px;
		}
	}
}

/*--------------------------------------*/
.side-menu{
	width: 325px;
	height: calc(100% - 108px);
	padding: 32px 0 32px 32px;
	flex-shrink: 0;
	transition: all 0.2s ease;
	position: absolute;
	left: 0;
	bottom: 0;
	background: $tertiary;
	z-index: 100;
	@include PC{
		width: 280px;
	}
	@include PC-sm{
		height: calc(100% - 54px);
		position: fixed;
	}
	&-header{
		font-size: $h4-font-size;
		color: $primary-xxdark;
		text-align: center;
		font-weight: $font-weight-semibold;
		width: calc(100% - 32px);
		@include PC{
			font-size: $h5-font-size;
		}
	}
	&-title{
		color: $primary-dark;
		width: calc(100% - 32px);
		border-bottom: 2px solid $white;
		padding-bottom: 12px;
		font-size: $h5-font-size;
		text-align: center;
		font-weight: $font-weight-semibold;
		margin-bottom: 0;
		@include PC{
			font-size: $h6-font-size;
		}
	}
	&-link{
		display: flex;
		align-items: center;
		color: $black;
		padding: 10px 16px;
		border-radius: 10px 0 0 10px;
		margin-bottom: 12px;
		position: relative;
		font-weight: $font-weight-medium;
		&::before{
			content:"";
			width: 10px;
			height: 10px;
			border-radius: 50%;
			background: $white;
			display: block;
			margin-right: 12px;
		}
		&:hover{
			opacity: 0.8;
		}
		&.active{
			background: $tertiary-xlight;
			margin-top: 12px;
			&::before{
				background: $bright-xxlight;
			}
			&:after{
				content: "";
				background: $tertiary-xlight;
				position: absolute;
				display: block;
				right: -32px;
				width: 32px;
				height: 100%;
				top: 0;
			}
		}
	}
	&-toggler {
		background: $tertiary;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 32px;
		padding: 12px 0;
		border-radius: 0 8px 8px 0 !important;
		position: absolute;
		right: -32px;
		top: 0px;
		line-height: 1.2;
		z-index: 10;
		transition: all 0.2s ease;
		border: none;
		&:hover{
			background: $tertiary !important;
		}
		&:active,&:focus{
			background: $tertiary !important;
			outline: none !important;
		}
		img {
			width: 18px;
			margin-right: 0;
			margin-bottom: 3px;
			transform: rotate(180deg);
		}
	}
	&.close {
		position: fixed;
		@include PC-sm {
			left: -280px;
		}
		.side-menu-toggler {
			img {
				transform: rotate(0deg);
			}
		}
	}
	&.fixed {
		position: fixed;
		top: 71px;
	}
}
.side-content {
	background: $white;
	padding: 32px 4% 24px 4%;
	width: 100%;
	margin-left: 325px;
	@include PC{
		margin-left: 280px;
	}
	@include PC-sm {
		margin-left: 0;
	}
	&-header{
		display: flex;
		align-items: center;
		@include PC-sm {
			flex-direction: column-reverse;
		}
	}
	&-title{
		color: $primary-dark;
		font-weight: $font-weight-semibold;
		span{
			border-left: 1px solid $primary;
			font-size: $h6-font-size;
			padding-left: 16px;
			margin-left: 16px;
			color: $gray-800;
		}
	}
}
.side-container{
	background: $white;
	border-radius: 10px;
	box-shadow: 0 3px 6px $gray-400;
}
.side-container-header{
	border-bottom: 2px solid $secondary-pale;
	z-index: 1;
	ul{
		display: flex;
	}
	li{
		width: 100%;
		@include PC-lg{
			width: auto;
			flex-shrink: 0;
		}
		&:first-child{
			a{
				background: none;
			}
		}
	}
	a{
		background: url("../images/ic-gonext.svg") no-repeat left center;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 20px;
		background-size: 10px;
		width: 100%;
		font-weight: $font-weight-bold;
		color: $gray-800;
		position: relative;
		@include PC-lg{
			padding: 20px 24px;
		}
		img{
			margin-right: 8px;
			width: 20px;
		}
		&.active{
			color: $secondary-dark;
			&::after{
				content: "";
				width: 100%;
				height: 5px;
				left: 0;
				bottom: 0;
				background: $secondary-pale;
				position: absolute;
			}
			img{
				filter: invert(56%) sepia(29%) saturate(722%) hue-rotate(96deg) brightness(97%) contrast(92%);
			}
		}
	}
}