/**/
#hello {
    @include mobile-lg {
        display: none;
    }
}
.main {
    &[data-con="apply"] {
        margin-top: 54px;
        padding-top: 100px;
        padding-bottom: 240px;
        background:
            url("../images/footer-bg-index-1.png") no-repeat left bottom,
            url("../images/footer-bg-index-2.png") no-repeat right bottom,
            url("../images/footer-bg-index-3.png") no-repeat center bottom;
        background-size: 1000px, 1091px, 100%;
        @include PC-md {
            background-size: 1000px, 1091px, 120%;
        }
        @include PC {
            background-size: 800px, 990px, 160%;
        }
        @include PC-sm {
            padding-top: 54px;
            background-size: 700px, 890px, 200%;
        }
        @include Pad {
            padding-top: 40px;
        }
        @include mobile-lg {
            background-size: 500px, 690px, 250%;
        }
        .main-title {
            /**/
            font-size: $h2-font-size;
            font-weight: $font-weight-heavy;
            color: $gray-600;
            text-align: center;
            margin-bottom: 32px;
            @include PC {
                font-size: $h3-font-size;
            }
            @include mobile-lg {
                font-size: $h4-font-size;
            }
        }
    }
}
#fileInput {
    border: 1px solid $special-light !important;
    text-align: right;
    &[type="file"]::-webkit-file-upload-button {
        float: right;
        position: relative;
        top: -1px;
        right: -10px;
        @include text-indent;
        background: url("../images/ic-cloud-arrow-up-fill.svg") no-repeat center $white;
        padding-left: 16px;
        border: none;
        width: 26px;
    }
}
.title-function {
    display: flex;
    align-items: center;
    font-size: $h6-font-size;
    font-weight: $font-weight-semibold;
    min-height: 26px;
    flex-wrap: wrap;
    .btn-outline-special {
        padding: 2px 10px !important;
        @include mobile-lg {
            padding: 2px 6px !important;
        }
        &:hover,
        &:active {
            color: $white !important;
        }
    }
}
.scrollbar-primary {
    &::-webkit-scrollbar {
        width: 8px;
        height: 8px;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 0;
        background-color: $primary;
    }
}
.font-number {
    font-family: "arial";
}
